
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    frame: {
      type: Array,
      default: () => ['head', 'body', 'foot'],
    },
    hasLine: {
      type: Boolean,
      default: () => false,
    },
  },
  setup(props) {
    const frameInclude = (type: string) => {
      if (props?.frame) {
        return props.frame.includes(type);
      }
      return false;
    };
    return {
      props,
      frameInclude,
    };
  },
});
